.header {
  width: 100%;
  height: 60px;
  border-bottom: 1px solid #000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.header h3 {
  font-weight: bold;
  padding-left: 20px;
}

.header .buttonGroup {
  margin-left: auto;
  display: -ms-flexbox;
  display: flex;
  padding-right: 20px;
}

.header .buttonGroup Button {
  padding: 0 50px 0 50px;
  margin: 0 5px 0 5px;
}

.deckContainer {
  margin: 20px 0 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  padding: 0 10% 0 10%;
  grid-column-gap: 10px;
  grid-row-gap: 40px;
}

.deckContainer .card {
  width: 90px;
  height: 120px;
  border: 1px solid black;
  margin: auto;
  position: relative;
}

.deckContainer .card .menuItemInner {
  margin: auto;
}


